.header {
  height: 60px;
  position: fixed;
  width: 100%;
  z-index: 50;
  top: 0;
  background-color: #5eae53;
}

.header__block {
  position: relative;
  height: 60px;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo h1 {
  font-size: 56px;
  line-height: 48px;
  color: #ffffff;
}

.header__menu {
  display: flex;
}
.header__menu li:not(:last-child) {
  padding-right: 14px;
}

.header__link {
  color: #ffffff;
}
.header__link:hover {
  opacity: 0.5;
}

.header__button {
  width: 170px;
  height: 48px;
  background-color: #5eae53;
  border: 1px solid rgba(255, 255, 255, 0.295743);
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 12px;
  color: #ffffff;
}
@media (max-width: 900px) {
  .header__button {
    width: 130px;
  }
}

.header__basket {
  display: flex;
}

.header__card {
  margin-left: 14px;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #eb8e78;
}
.header__card img {
  display: block;
}

.content {
  margin-top: 60px;
}

.hamburger {
