    top: 0;
    background-color: #5eae53;
  }

  .hamburger {
    display: block;
    position: fixed;
    z-index: 50;
    right: 0;
    top: 0;
  }

  .header__block {
    position: fixed;
    top: -100%;
    left: 0;
    z-index: 50;
    width: 100%;
    height: 100%;
    background-color: #afd3aa;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
